@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
.empty {
    height: 100vh;
    width: 100vw;
    background-color: #e7e7e7;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.empty span {
    padding: 30px;
    background-color: white;
    box-shadow: 0px 0px 10px #bebebe;
}
.bingo-page {
    background-color: #e7e7e7;
    display: -webkit-flex;
    display: flex;
    height: 100%;
    min-height: 100vh;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.bingo-page__header {
    margin-top: 30px;
    background-color: white;
    box-shadow: 0px 0px 10px #bebebe;
    padding: 30px;
    max-width: 70%;
}

.bingo-page__title {
    font-size: 1.5em;
    border-bottom: solid 1px #ccc;
    margin-bottom: 20px;
}

.bingo-page__title strong {
    font-size: 2em;
    font-weight: 900;
}

.bingo-page__title p {
    display: block;
}

.bingo-page__container {
    margin-top: 30px;
    background-color: white;
    box-shadow: 0px 0px 10px #bebebe;  
    padding: 30px;
    width: 70%;
    max-width: 70%;
}

.bingo-page__container-header strong{
    margin:  30px;
}

.bingo-page__container-body {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.bingo-page__container-body p {
    margin: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.bingo-page__container-body span {
    -webkit-align-items: center;
            align-items: center;
    border: solid 1px #ccc;
    display: -webkit-flex;
    display: flex;
    font-size: 3rem;
    height: 2em;
    -webkit-justify-content: center;
            justify-content: center;
    width: 2em;
    margin-bottom: 10px;
    cursor: pointer;
}

.bingo-page__container-body strong {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    font-size: 3rem;
    font-weight: 900;
    height: 2em;
    -webkit-justify-content: center;
            justify-content: center;
    width: 2em;
}

.bingo-page__footer {
    margin: 30px 0;
    font-size: 1.5em;
}

.item {
    transition: .3s;
}

.item:hover {
    background-color: #ccc;
}

.selected {
    transition: .3s;
    background-color: green;
    color: white;
}

@media (max-width: 870px) {
    .bingo-page__container {
        margin-bottom: 20px;
    }

    .bingo-page__container-body {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .bingo-page__container-body p {
        -webkit-flex-direction: row;
                flex-direction: row;
    }

    .bingo-page__container-body span {
        width: 1.5em;
        height: 1.5em;
        margin-bottom: 0;
        margin-right: 5px;
    }

    .bingo-page__container-body strong {
        width: 1.5em;
        height: 1.5em;
    }
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	font-family: 'Indie Flower', 'sans-serif';
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
